html {
  color-scheme: light dark;
  --bg-color: #f0f0f0;
  --text-color: #000;
  --toggle-content: "☀️";
  font-family: system-ui;

  &[data-theme="light"] {
    --bg-color: #f0f0f0;
    --text-color: #000;
    --toggle-content: "☀️";
  }

  &[data-theme="dark"] {
    --bg-color: #000;
    --text-color: #f0f0f0;
    --toggle-content: "🌙";
  }

  & body {
    background: var(--bg-color);
    color: var(--text-color);
  }

  & #theme-toggle {
    position: fixed;
    top: 5px;
    right: 5px;

    &:after {
      content: var(--toggle-content);
    }
  }
}

.App {
  --df-green: #10f898;
  --df-quality-common: #c4c4c2;
  --df-quality-uncommon: #2aca96;
  --df-quality-rare: #589fdc;
  --df-quality-epic: #9a71dc;
  --df-quality-legendary: #d0814d;
  --df-quality-exotic: #da5758;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;

  & .sections {
    flex-flow: wrap;
    gap: 2em;
    padding-top: 40px;
    display: flex;

    & .section {
      & h2 {
        margin: 0 0 1em;
        padding: 0;
        font-size: 24px;
      }
    }
  }

  & .MapTimers {
    & .currentTime {
      margin-bottom: .5em;
      font-size: 18px;
      font-weight: bold;
    }

    & .timers {
      & .timer {
        border-left: 5px solid var(--df-green);
        padding: .5em;

        & h3 {
          margin: 0;
          padding: 0;
          font-size: 24px;
        }

        &.live {
          background: var(--df-green);
          color: #000;
          font-weight: bold;
        }

        & .time {
          font-size: 18px;
        }
      }
    }
  }

  & .Collector {
    & .mission {
      & h3 {
        margin-bottom: .5em;
        font-size: 18px;
        font-weight: bold;
      }

      & .items {
        & .item {
          --item-color: transparent;
          border-left: 4px solid #0000;
          border-color: var(--item-color);
          width: 100%;
          box-shadow: inset 100px 0 100px -100px var(--item-color);
          flex-flow: row;
          gap: 1em;
          margin-bottom: 4px;
          padding: 2px 0 2px .5em;
          display: flex;

          &.common {
            --item-color: var(--df-quality-common);
          }

          &.uncommon {
            --item-color: var(--df-quality-uncommon);
          }

          &.rare {
            --item-color: var(--df-quality-rare);
          }

          &.epic {
            --item-color: var(--df-quality-epic);
          }

          &.legendary {
            --item-color: var(--df-quality-legendary);
          }

          &.exotic {
            --item-color: var(--df-quality-exotic);
          }

          & .name {
            flex-grow: 1;
          }

          & .buttons {
            flex-flow: row;
            gap: 4px;
            display: flex;
            overflow: hidden;

            & .button {
              cursor: pointer;
              color: #000;
              -webkit-user-select: none;
              user-select: none;
              justify-content: center;
              align-items: center;
              width: 24px;
              height: 24px;
              font-size: 32px;
              font-weight: bold;
              display: flex;

              &.add {
                background: var(--df-green);
              }

              &.remove {
                background: var(--df-quality-exotic);
              }

              & > span {
                transform: translateY(-4px);
              }

              &.disabled {
                cursor: not-allowed;
                background: var(--df-quality-common);
              }
            }
          }
        }
      }
    }
  }
}
